let now = new Date();
let year = now.getFullYear();
let Month = now.getMonth() + 1;
let Day = now.getDate();
if (Month < 10) { Month = "0" + Month; } if (Day < 10) { Day = "0" + Day; }


const postList = [
  {
    "no": 1,
    "title": "  ",
    "content": "     ,  3000.",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 2,
    "title": "  ",
    "content": "      ,  @@@  ",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 3,
    "title": " .",
    "content": "  .",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 4,
    "title": " .",
    "content": "  .",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
];

const getPostByNo = no => {
  const array = postList.filter(x => x.no == no);
  if (array.length == 1) {
    return array[0];
  }
  return null;
}

export {
  postList,
  getPostByNo
};